/* ========= End common Styles ========= */
/* =========== Start Header ===========  */

header {
  position: relative;
  margin-bottom: 25px;
  &::after {
    content: "";
    position: absolute;
    height: 1px;
    width: 100%;
    bottom: 25px;
    background: var(--cultured-clr);
  }
  .content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
  }
  .logo {
    position: absolute;
    bottom: -34px;
    font-size: .9375rem;
    width: fit-content;
    padding: 5px 10px;
    border-radius: 50%;
    border: 2px solid var(--primary-clr);
    border-left-style: dotted;
    color: var(--black-clr);
  }
  .social__media {
    display: flex;
    align-items: center;
    column-gap: 20px;
    a {
      display: block;
      width: 20px;
    }
  }
  .options {
    display: flex;
    align-items: center;
    position: relative;
    column-gap: 10px;
    &::before {
      content: '';
      position: absolute;
      height: 22px;
      width: 1px;
      left: 35%;
      bottom: 15px;
      background-color: #6D6E6E;
    }
    .theme__mode {
      width: 40px;
      border: none;
      cursor: pointer;
      background-color: transparent;
    }
    .website__language {
      display: flex;
      align-items: center;
      cursor: pointer;
      &:hover {
        ul {
          display: block !important;
        }
      }
      .flag {
        position: relative;
        width: 30px;
        ul {
          position: absolute;
          display: none;
          width: 61px;
          top: 0;
          z-index: 2;
          text-align: center;
          background-color: #6D6E6E;
          li {
            &:hover {
              background-color: #0059B2;
            }
            a {
              display: inline;
              font-size: 10px;
              span {
                color: white;
                padding-left: 5px;
                position: relative;
                top: -2px;
              }
            }
          }
        }
      }
      .arrow {
        width: 10px;
      }
    }
  }
  .toggle__manu {
    position: relative;
    width: 30px;
    bottom: -9px;
    margin-left: auto;
    cursor: pointer;
    span {
      display: block;
      width: 100%;
      height: 2px;
      transition: var(--transition);
      background-color: var(--black-clr);
    }
    span:not(:last-child) {
      margin-bottom: 5px;
    }
  }
  .toggle__manu.open {
    span:first-child {
      transform: rotate(45deg) translate(6px, 3px);
      background-color: var(--red-color);
    }
    span:nth-child(2) {
      transform: translateX(-15px);
      opacity: 0;
    }
    span:last-child {
      transform: rotate(-45deg) translate(6px, -5px);
      background-color: var(--red-color);
    }
  }
}

/* =========== End Header =============  */
